@import "../../../_defaults.scss";

div.TrainItem {
  padding: $base-margin-small;
  margin: $base-margin-small;

  background-color: $color-black-light;
  border-radius: $base-border-radius;

  display: grid;

  grid-template-columns: 3em auto 3em;
  grid-template-rows: auto;

  grid-template-areas:
    "controls title settings"
    "o-d o-d o-d"
    "timetable timetable timetable"
    "status info info";

  &.NEW {
    border: 2px solid blue;
  }

  &.UPDATED {
    border: 2px solid green;
  }

  div.controls {
    grid-area: controls;
    overflow: none;
  }

  div.title {
    grid-area: title;
    text-align: center;
    font-weight: $font-weight-bold;
  }

  div.settings {
    grid-area: settings;
    text-align: right;
  }

  div.o-d {
    grid-area: o-d;
    text-align: center;
  }

  div.timetable {
    grid-area: timetable;

    table {
      width: 100%;

      tr.STOP {

      }

      tr.PASS {
        visibility: collapse;
      }

      tr.currentStation {
        background-color: darkred;
      }

      tr.currentStation.PASS {
        visibility: visible;
        color: red;
      }
    }
  }

  div.status {
    grid-area: status;
  }

  div.info {
    grid-area: info;
  }
}
